
.profilePhoto {
  padding-bottom: 40px;
  border-bottom: 1px solid #d2d2d2;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 40px;

  .photoBlock {
    position: relative;
    width: 150px;
    height: 150px;
    .userPhoto {
      border: 3px solid #adadad;
      border-radius: 50%;
      width: 150px;
      padding: 1px;

    }

    .divinput {
      position: absolute;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: #2c3136;
      opacity: 30%;
      color: black;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 36px;
      cursor: pointer;
      bottom: 0;
      right: 0;

      &:hover {
        opacity: 60%;
      }

      .photoInput[type=file] {
        display: none;
      }
    }

  }
  .nameStatus {
    margin-left: 30px;
    max-width: 400px;
    width: 100%;
    .profileHeader {
      margin-bottom: 20px;
      font-size: 24px;
      color: #2a2e33;
      font-weight: 800;
      font-family: "Montserrat", sans-serif;
    }
  }

}
